<template>
  <div>
    <app-header-row :item="service"></app-header-row>
    <app-alert :alert="hasError" @closeAlert="hasError = false">{{ error }}</app-alert>
    <app-data-table v-if="loaded" :headers="headers" :items="items"></app-data-table>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import Alert from '../blocks/Alert'
import DataTable from './DataTable'
import HeaderRow from './HeaderRow'

export default {
  name: 'PersonalData',
  props: {
    service: { required: true },
    method: { required: true }
  },
  data: () => ({
    hasError: false
  }),
  components: {
    appAlert: Alert,
    appDataTable: DataTable,
    appHeaderRow: HeaderRow
  },
  watch: {
    method () {
      this.updateData()
    },
    error (val) {
      this.showAlert({
        text: val
      })
    }
  },
  methods: {
    ...mapActions('personalData', {
      getData: 'getData'
    }),
    ...mapActions(['show_preloader']),
    updateData () {
      this.show_preloader()
      this.getData(this.method)
    }
  },
  computed: {
    ...mapGetters('personalData', {
      error: 'error',
      headers: 'headers',
      items: 'items',
      loaded: 'loaded'
    })
  },
  created () {
    this.updateData()
  }
}
</script>

